var that;
export default {
	data() {
		return {
			list: [],
			lodstatus: '',
			page: 1,
			nolist:false
		};
	},
	methods: {
		tpinfo(e) {
			uni.navigateTo({
				url: './infoList/infoList?course_id='+e.currentTarget.dataset.id
			});
		},
		getlist(e) {
			let page = that.page;
			that.lodstatus = 'loading';
			that.nolist=false;
			that.$request
				.request({
					url: that.api.college.course_video_list,
					method: 'get',
					data: {
						page: page
					}
				})
				.then(res => {
					let list = res;
					if (page == 1) {
						that.list = list;
						that.page++; 
						list.length==0?(that.lodstatus = '',that.nolist=true):(list.length>10?that.lodstatus = 'more':that.lodstatus = '')
					} else {
						if (list.length > 0) {
							that.list = that.list.concat(list);
							that.page++;
							that.lodstatus = 'more'; 
						} else {
							that.lodstatus = 'noMore';
							that.nolist=true 
						}
					}
					uni.hideLoading();
					uni.stopPullDownRefresh();
				});
		}
	},
	onLoad() {
		that = this;
		that.getlist();
	},
	onPullDownRefresh() {
		that.page=1;
		that.getlist();
	}
};
